import React from 'react';
import colors from '../../assets/colors';
import { Link } from 'react-router-dom';
import MyIcon from '../MyIcon';
import { NormalBtn } from '../Btn';
class NavbarSView extends React.Component{
    constructor(props){
        super(props);
        this.state={
            isMenuOpen:false,
            login:global.login
        }
    }
    toggleSViewMenu(){
        if(this.state.isMenuOpen){
            this.setState({isMenuOpen: false});
            document.getElementById("body").style.overflow = "unset";
        }else{
            this.setState({isMenuOpen: true});
            document.getElementById("body").style.overflow = "hidden";
        }
    }
    render(){
        return(
            <div className="NavbarSView">
                <div className="NavbarSViewContainer">
                    <div>
                        {this.props.page === "Home" && <small className="FT2" style={{fontFamily:global.font().HeeboRegular,color:colors.red}}>Hayatemoon</small>}
                        {this.props.page !== "Home" && <Link to="/"><div className="Pointer BackBtnNav">
                            <img style={{marginRight:10}} src={require('../../assets/icons/arrowLeftRed.svg')} width="15" height="15" /> 
                            <small className="FT2" style={{fontFamily:global.font().HeeboRegular,color:colors.red}}>Back</small>
                        </div></Link>}
                    </div>
                    <div><Link to="/"><img src={require('../../assets/images/logo.png')} width="27" height="27" /></Link></div>
                    <div className="ItemNavContainer Rtl">
                        <img className="Pointer" onClick={()=>this.toggleSViewMenu()} style={{marginLeft:"5vw"}} src={require('../../assets/icons/menu.svg')} width="25" height="25" />
                        {this.props.page === "searchRestaurant" && <img onClick={()=>this.props.FilterClick()} className="Pointer" style={{marginLeft:"5vw"}} src={require('../../assets/icons/filterRed.svg')} width="25" height="25" />}
                        {this.props.page === "searchRestaurant" && <img onClick={()=>this.props.LocationClick()} className="Pointer" style={{marginLeft:"5vw"}} src={require('../../assets/icons/place.svg')} width="25" height="25" />}
                    </div>
                </div>

                {/* Modal View Start */}
                <div className={this.state.isMenuOpen ? "ModalMenuSView ModalMenuSViewActive" : "ModalMenuSView"}>
                    <div className="BlurMenuSView">
                        <div className="ContainerMenuSView animated slideInRight">
                            <div className="Flex VCenter" style={{justifyContent:"space-between"}}>
                                {!this.props.login && <div className="Flex">
                                    <Link to={this.state.login ? "/dashboard" : "/userRegistration"}><NormalBtn onClick={()=>{}} bgColor={colors.lightNavyBlue} title={this.state.login ? "Samuel Jackson" : "SignUp"} color={colors.white} /></Link>
                                </div>}
                                <div>
                                    <NormalBtn onClick={()=>this.toggleSViewMenu()} bgColor={colors.white}>
                                        <MyIcon icon={require('../../assets/icons/close.svg')} alt="CloseMenu" />
                                    </NormalBtn>
                                </div>
                            </div>
                            <div>
                                <ul className="menuNavUlSView animated fadeInRight">
                                    <li className="FMH2" onClick={()=>{this.toggleSViewMenu()}} style={{fontFamily:global.font().PatuaOneRegular}}><a href="http://hayatemoon.com/">Ad-Hayatemoon</a></li>
                                    {this.state.login && <li className="FMH2" onClick={()=>{this.toggleSViewMenu()}} style={{fontFamily:global.font().PatuaOneRegular}}><Link to="/dashboard">Dashboard</Link></li>}
                                    {this.state.login && <li className="FMH2" onClick={()=>{this.toggleSViewMenu()}} style={{fontFamily:global.font().PatuaOneRegular}}><Link to="/shopping">Increase Credit</Link></li>}
                                    {this.state.login && <li className="FMH2" onClick={()=>{this.toggleSViewMenu()}} style={{fontFamily:global.font().PatuaOneRegular}}><Link to="/dashboardInvocie">My Orders</Link></li>}
                                    {this.state.login && <li className="FMH2" onClick={()=>{this.toggleSViewMenu();global.login = false}} style={{fontFamily:global.font().PatuaOneRegular}}><Link to="/">Exit</Link></li>}
                                </ul>
                            </div>
                            <div>
                                <ul className="changeLanMenuSView">
                                    <li className="FMH2" onClick={()=>{}} style={{fontFamily:global.font().PatuaOneRegular}}>En</li>
                                    <li className="FMH2" onClick={()=>{}} style={{fontFamily:global.font().PatuaOneRegular}}>Tr</li>
                                    <li className="FMH2" onClick={()=>{}} style={{fontFamily:global.font().PatuaOneRegular}}>فا</li>
                                </ul>
                            </div>
                        </div>
                    </div>
                </div>
                {/* Modal View End */}

            </div>
        )
    }
}

export default NavbarSView;