import React from 'react';
import colors from '../assets/colors';
import { Container , Row } from 'react-bootstrap';
import { BlurBtn, NormalBtn } from '../components/Btn';
import MyIcon from '../components/MyIcon';
import { Link } from 'react-router-dom';
import $ from 'jquery';
class Navbar extends React.Component{
    constructor(props){
        super(props);
        this.state={
            login : global.login,
            isMenuOpen: false
        }
    }
    toggleMenu(){
        var circleMenu = document.getElementById("menuOpenBtn");
        document.getElementById("circleMenu").style.left = `${circleMenu.offsetLeft}px`;
        document.getElementById("circleMenu").style.top = `${circleMenu.offsetTop}px`;
        if(this.state.isMenuOpen){
            this.setState({isMenuOpen : false})
            document.getElementById("circleMenu").style.width = "50px";
            document.getElementById("circleMenu").style.height = "50px";
            document.getElementById("body").style.overflow = "unset";
        }else{
            this.setState({isMenuOpen : true})
            document.getElementById("body").style.overflow = "hidden";
            document.getElementById("circleMenu").style.width = "1300px";
            document.getElementById("circleMenu").style.height = "1300px";
            document.getElementById("circleMenu").style.left = `${circleMenu.offsetLeft-650}px`;
            document.getElementById("circleMenu").style.top = `${circleMenu.offsetTop-650}px`;
        }
    }
    componentDidMount(){
        $(window).scroll(()=>{
            if($(window).scrollTop() > 100){
                $('.LogoType').addClass('fadeOutUp').removeClass('fadeInUp')
            }else{
                $('.LogoType').addClass('fadeInUp').removeClass('fadeOutUp')
            }
        })
    }
    render(){
        return(
            <div className="Navbar">
                <div className="Flex HCenter VCenter">
                    <Container>
                        <Row>
                            <div className="Flex Row VCenter W100" style={{justifyContent:"space-between"}}>
                                <div className="ItemNavbar Flex Row VCenter">

                                    {/* AdSite-hayetemoonBtn */}
                                    <div style={{margin:"0px 8px"}}>
                                        <BlurBtn bgColor={colors.whiteBlur}>
                                            <MyIcon icon={require('../assets/icons/ad.svg')} alt="AdSite-hayetemoon"  />
                                        </BlurBtn>
                                    </div>

                                    {/* ChangeLanguageBtn */}
                                    <div style={{margin:"0px 8px"}}>
                                        <BlurBtn bgColor={colors.whiteBlur} >
                                            <MyIcon icon={require('../assets/icons/global.svg')} alt="changeLanguages"  />
                                        </BlurBtn>
                                    </div>

                                    {/* UserBtn */}
                                    {this.state.login && 
                                        <div style={{margin:"0px 8px",zIndex:this.state.isMenuOpen ? 50 : "unset"}}>
                                            {!this.state.isMenuOpen && <Link to="/dashboard"><BlurBtn onClick={()=>{this.state.isMenuOpen && this.toggleMenu()}} bgColor={colors.whiteBlur} color={colors.red} title="Samuel Jackson"/></Link>}
                                            {this.state.isMenuOpen && <Link to="/dashboard"><NormalBtn onClick={()=>{this.state.isMenuOpen && this.toggleMenu()}} className="animated fadeIn" bgColor={colors.navyBlue} color={colors.white} title="Samuel Jackson"/></Link>}
                                        </div>
                                    }
                                    
                                    {/* MenuBtn */}
                                    {this.state.login && 
                                        <div id="menuOpenBtn" style={{margin:"0px 8px",zIndex:this.state.isMenuOpen ? 50 : "unset"}}>
                                            <BlurBtn bgColor={this.state.isMenuOpen ? colors.white : colors.whiteBlur}  onClick={()=>{this.toggleMenu()}}>
                                                <MyIcon 
                                                    icon={this.state.isMenuOpen ? require('../assets/icons/close.svg') : require('../assets/icons/menu.svg')} 
                                                    alt="menu"
                                                />
                                            </BlurBtn>
                                        </div>
                                    }

                                    {/* SignUpBtn */}
                                    {!this.state.login && <div style={{margin:"0px 8px"}}><Link to="/userRegistration"><BlurBtn onClick={()=>{}} bgColor={colors.whiteBlur} color={colors.red} title="Sign up"/></Link></div>}
                                
                                </div>

                                {/* Logo */}
                                <div className="Logo Flex VCenter Ltr">
                                    <Link to="/">
                                        <button className="LogoNav" style={{backgroundColor: colors.whiteBlur}}>
                                            <img src={require('../assets/images/logo.png')} alt="HayatemoonLogo" />
                                        </button>
                                    </Link>
                                    <div className="LogoType animated">
                                        <h1 className="FH2" style={{color:colors.darkNavyBlue,fontFamily:global.font().PatuaOneRegular}}>Hayatemoon</h1>
                                        <div className="Flex VCenter" style={{justifyContent:"space-between"}}>
                                            <small className="FT1" style={{color:colors.darkNavyBlue,fontFamily:global.font().HeeboLight}}>
                                                Online
                                            </small>
                                            <small className="FT1" style={{color:colors.darkNavyBlue,fontFamily:global.font().HeeboLight}}>
                                                Food
                                            </small>
                                            <small className="FT1" style={{color:colors.darkNavyBlue,fontFamily:global.font().HeeboLight}}>
                                                Order
                                            </small>
                                        </div>
                                    </div>
                                </div>

                            </div>
                        </Row>
                    </Container>

                    {/* Open MenuNav Start */}
                    <div className="ModalOpenMenu" style={{visibility:this.state.isMenuOpen ? "visible" : "hidden"}}>
                        <div className={this.state.isMenuOpen ? "ModalOpenMenuBackground ModalOpenMenuBackgroundActive" : "ModalOpenMenuBackground"}></div>
                        <div className="circleMenu" id="circleMenu"></div>
                        <Container>
                            <Row>
                            <ul className={this.state.isMenuOpen ? "menuNavUl animated fadeInRight" : "menuNavUl"}>
                                <Link to="/dashboard"><li onClick={()=>{this.toggleMenu()}} className="FH1" style={{fontFamily:global.font().PatuaOneRegular}}>Dashboard</li></Link>
                                <Link to="/shopping"><li onClick={()=>{this.toggleMenu()}} className="FH1" style={{fontFamily:global.font().PatuaOneRegular}}>Increase Credit</li></Link>
                                <Link to="/dashboardInvocie"><li onClick={()=>{this.toggleMenu()}} className="FH1" style={{fontFamily:global.font().PatuaOneRegular}}>My Orders</li></Link>
                                <Link to=""><li onClick={()=>{this.toggleMenu();global.login = false}} className="FH1" style={{fontFamily:global.font().PatuaOneRegular}}>Exit</li></Link>
                            </ul>
                            </Row>
                        </Container>
                        
                    </div>
                    {/* Open MenuNav End */}

                </div>
            </div>
        )
    }
}

export default Navbar;