import React from 'react'
import NavbarSView from '../../components/smallView/NavbarSView';
import colors from '../../assets/colors';
import MyInput from '../../components/MyInput';
import { NormalBtn } from '../../components/Btn';
import { Link } from 'react-router-dom';

class UserRegistrationSView extends React.Component{
    constructor(props){
        super(props);
        this.state={
            isLogin:true,
            isSignUp:false,
            isForgotPassword:false,
            isResetPassWord:false
        }
    }
    toggleSection(section){
        switch (section) {
            case "Login":
                this.setState({isLogin:true,isSignUp:false,isForgotPassword:false,isResetPassWord:false});
                break;
            case "SignUp":
                this.setState({isLogin:false,isSignUp:true,isForgotPassword:false,isResetPassWord:false});
                break;
            case "ForgotPassword":
                this.setState({isLogin:false,isSignUp:false,isForgotPassword:true,isResetPassWord:false});
                break;
            case "ResetPassWord":
                this.setState({isLogin:false,isSignUp:false,isForgotPassword:false,isResetPassWord:true});
                break;
        }
    }
    render(){
        return (
            <div className="UserRegistrationSView">
                <NavbarSView />
                <div className="BlurUserRegistrationSView Flex VCenter Col">
                    {(this.state.isSignUp) &&<div className="SignUpSView Flex VCenter Col W100">
                        <h5 className="FMH1 animated zoomIn" style={{color:colors.red,fontFamily:global.font().PatuaOneRegular}}>Sign up</h5>
                        <div className="W100" style={{marginTop:"10%"}}>
                            <div style={{marginTop:10}}><MyInput placeholder="Username" alt="Username" color={colors.lightGray} /></div>
                            <div style={{marginTop:10}}><MyInput placeholder="Email" alt="Email" color={colors.lightGray} /></div>
                            <div style={{marginTop:10}}><MyInput placeholder="Password" alt="Password" color={colors.lightGray} /></div>
                            <div style={{marginTop:10}}><MyInput placeholder="Repeat Password" alt="Password" color={colors.lightGray} /></div>
                        </div>
                        <div className="animated zoomIn" style={{marginTop:25}}>
                            <Link to="/dashboard"><NormalBtn onClick={()=>{global.login = true}} title="Sign up" SView color={colors.white}  bgColor={colors.red} /></Link>
                        </div>
                        <div className="Flex W100" style={{justifyContent:'flex-start',marginTop:40}}>
                            <p className="FMT2" style={{color:colors.lightGray,fontFamily:global.font().HeeboRegular}}>have an account?<span onClick={()=>{this.toggleSection("Login")}} style={{color:colors.darkNavyBlue}}> Login </span>here</p>
                        </div>
                    </div>}
                    {(this.state.isLogin) &&<div className="LoginSView Flex VCenter Col W100">
                        <h5 className="FMH1 animated zoomIn" style={{color:colors.darkNavyBlue,fontFamily:global.font().PatuaOneRegular}}>Login</h5>
                        <div className="W100" style={{marginTop:"10%"}}>
                            <div style={{marginTop:10}}><MyInput placeholder="Username" alt="Username" color={colors.lightGray} /></div>
                            <div style={{marginTop:10}}><MyInput placeholder="Password" alt="Password" color={colors.lightGray} /></div>
                        </div>
                        <div className="animated zoomIn" style={{marginTop:25}}>
                            <Link to="/dashboard"><NormalBtn onClick={()=>{global.login = true}} title="Log in" SView color={colors.white} bgColor={colors.darkNavyBlue} /></Link>
                        </div>
                        <div className="Flex W100" style={{justifyContent:'flex-start',marginTop:50}}>
                            <p className="FMT2" onClick={()=>{this.toggleSection("ForgotPassword")}} style={{color:colors.darkNavyBlue,fontFamily:global.font().HeeboRegular,margin:"5px 0px"}}>Forgot password?</p>
                        </div>
                        <div className="Flex W100" style={{justifyContent:'flex-start'}}>
                            <p className="FMT2" style={{color:colors.lightGray,fontFamily:global.font().HeeboRegular}}>Don't Register yet?<span onClick={()=>{this.toggleSection("SignUp")}} style={{color:colors.darkNavyBlue}}> Sign up </span>here</p>
                        </div>
                    </div>}
                    {(this.state.isForgotPassword) &&<div className="ForgotPasswordSView Flex VCenter Col W100">
                        <h5 className="FMH1 animated zoomIn" style={{color:colors.darkNavyBlue,fontFamily:global.font().PatuaOneRegular}}>Forgot password</h5>
                        <small className="FMT3 TCenter" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray,marginTop:15}}>
                            Enter your email you using for your<br/> account and we will send you a link to<br/> reset your password.
                        </small>
                        <div className="W100" style={{marginTop:"10%"}}>
                            <div style={{marginTop:10}}><MyInput placeholder="Email" alt="Email" color={colors.lightGray} /></div>
                        </div>
                        <div className="animated zoomIn" style={{marginTop:25}}>
                            <NormalBtn onClick={()=>this.toggleSection("ResetPassWord")} title="Request Reset Link" SView color={colors.white} bgColor={colors.darkNavyBlue} />
                        </div>
                        <div className="Flex W100" style={{justifyContent:'flex-start',marginTop:15}}>
                            <p className="FMT2" style={{color:colors.lightGray,fontFamily:global.font().HeeboRegular}}>If email wasn't send,<span style={{color:colors.darkNavyBlue}}>Resend</span></p>
                        </div>
                        <div className="Flex W100" style={{justifyContent:'flex-start',marginTop:40}}>
                            <p className="FMT2" onClick={()=>{this.toggleSection("Login")}} style={{color:colors.darkNavyBlue,fontFamily:global.font().HeeboRegular,margin:"5px 0px"}}>back to login</p>
                        </div>
                    </div>}
                    {(this.state.isResetPassWord) &&<div className="ForgotPasswordSView Flex VCenter Col W100">
                        <h5 className="FMH1 animated zoomIn" style={{color:colors.darkNavyBlue,fontFamily:global.font().PatuaOneRegular}}>Reset pass</h5>
                        <small className="FMT3 TCenter" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray,marginTop:15}}>
                            Choose your password wisely.
                        </small>
                        <div className="W100" style={{marginTop:"10%"}}>
                            <div style={{marginTop:10}}><MyInput placeholder="Password" alt="Password" color={colors.lightGray} /></div>
                            <div style={{marginTop:10}}><MyInput placeholder="Repeat Password" alt="Password" color={colors.lightGray} /></div>
                        </div>
                        <div className="animated zoomIn" style={{marginTop:25}}>
                            <NormalBtn onClick={()=>{}} title="Reset Password" SView color={colors.white} bgColor={colors.darkNavyBlue} />
                        </div>
                    </div>}
                </div>
            </div>
        )
    }
}

export default UserRegistrationSView;