import React from 'react'
import colors from '../../assets/colors';
import { NormalBtn } from '../Btn'
import { Row , Col } from 'react-bootstrap'
class Transactions extends React.Component{
    constructor(props){
        super(props);
        this.state={
            
        }
    }
    render(){
        return (
            <div className={this.props.SView ? "TransactionsItemContainer Flex" : "TransactionsItem"}>
                {this.props.SView && <div className="TransactionsItem" style={{backgroundColor:colors.lightestPink,marginRight:0}}>
                    <Row>
                        <Col md={5} lg={5}>
                            <small className={this.props.SView ? "FMT2" : "FT2"} style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>Restaurant Name</small>
                        </Col>
                        <Col md={3} lg={3}>
                            <small className={this.props.SView ? "FMT2" : "FT2"} style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>Date</small>
                        </Col>
                        <Col md={2} lg={2}>
                            <small className={this.props.SView ? "FMT2" : "FT2"} style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>Time</small>
                        </Col>
                        <Col md={2} lg={2}>
                            <small className={this.props.SView ? "FMT2" : "FT2"} style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>Price</small>
                        </Col>
                    </Row>
                </div>}
                <Row style={{padding:this.props.SView ? "20px 15px" : 0,marginLeft:this.props.SView ? 15 : 0,marginRight:0,backgroundColor:this.props.SView ? colors.pink : "unset",marginTop : this.props.SView ? 15 : "unset",borderRadius:this.props.SView ? 20 : "unset"}}>
                    <Col md={5} lg={5}>
                        <small className="FT2" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>{this.props.name}</small>
                    </Col>
                    <Col md={3} lg={3}>
                        <small className="FT2" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>{this.props.date}</small>
                    </Col>
                    <Col md={2} lg={2}>
                        <small className="FT2" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>{this.props.time}</small>
                    </Col>
                    <Col md={2} lg={2}>
                        <small className="FT2" style={{fontFamily:global.font().HeeboRegular,color:colors.gray}}>$ {this.props.price}</small>
                    </Col>
                </Row>
            </div>
        )
    }
}

export default Transactions; // name , price , date , time 