import React from 'react';
import { RateBtn } from './Btn';
import colors from '../assets/colors';

class RestautrantInfo extends React.Component{
    constructor(props){
        super(props);
        this.state={}
    }
    render(){
        return(
            <div className="RestaurantInfoContainer Flex" style={{justifyContent:"space-between"}}>

                
                <div className="Flex">
                    <div style={{width:"30%"}} >
                        <img src={require('../assets/images/ResLogo-01.png')} style={{width:"100%",borderRadius:25,maxWidth:300}}/>
                    </div>

                    <div className="Flex Col" style={{justifyContent:"space-between",marginLeft:15}}>
                        <div>
                            <div className="Flex VCenter NameInfoRes">
                                <h1 className="FH1" style={{margin:0,fontFamily:global.font().PatuaOneRegular,color:colors.darkNavyBlue}}>Robin Food</h1>
                                <small className="FT2 Pointer" style={{margin:"0px 15px",fontFamily:global.font().HeeboRegular,color:colors.darkGray,opacity:0.5}}>Another branchs<img style={{opacity:1,margin:"0px 10px"}} src={require('../assets/icons/arrowLeftDarkNavy.svg')} width="12" height="12" /></small>
                            </div>
                            <small className="FT2" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>Fastfood</small>
                        </div>
                        <small className="FT2" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>Istanbul, Baqdad Ave, Medmedov St.</small>
                    </div>
                </div>
                <div className="Flex Col" style={{justifyContent:"space-between"}}>
                    <div className="Flex VCenter" style={{padding:"18px 0px"}}>
                        <small className="FT2 Pointer" style={{fontFamily:global.font().HeeboRegular,color:colors.darkGray}}>
                            <img style={{margin:"0px 10px"}} src={require('../assets/icons/clock.svg')} width="25" height="25" />Another branchs
                        </small>
                    </div>
                    <div className="RatingResView">
                        <div style={{marginRight:30}}>
                            <img src={require('../assets/icons/account.svg')} width="20" height="20" style={{marginRight:10}} />
                            <small className="FT2" style={{fontFamily:global.font().HeeboRegular,color:colors.lightGray}}>2453</small>
                        </div>
                        <RateBtn color={colors.white} amount={4.5} alt="rating" />
                    </div>
                </div>
                
            </div>
        )
    }
}

export default RestautrantInfo;