import React from 'react';
import { NormalBtn } from '../Btn';
import colors from '../../assets/colors';
import MyInput from '../MyInput';
class Security extends React.Component{
    constructor(props){
        super(props);
        this.state={
        }
    }
    render(){
        return (
            <div className="Security" style={{width:this.props.SView ? "100%" : "80%"}}>
                <div style={{marginTop: 10,padding:"0px 8px"}}><MyInput placeholder="Username" alt="Username" color={colors.lightGray} /></div>
                <div style={{marginTop: 10,padding:"0px 8px"}}><MyInput placeholder="Old Password" alt="password" color={colors.lightGray} /></div>
                <div style={{marginTop: 10,padding:"0px 8px"}}><MyInput placeholder="New Password" alt="password" color={colors.lightGray} /></div>
                <div style={{marginTop: 10,padding:"0px 8px"}}><MyInput placeholder="Repeat New Password" alt="password" color={colors.lightGray} /></div>
                <div className="Flex VCenter" style={{justifyContent:this.props.SView ? "space-between" : "flex-end",marginTop:20}}>
                    <div style={{margin:this.props.SView ? "10px 0px" : "0px 10px"}}><NormalBtn SView={this.props.SView} title="Confirm" bgColor={colors.darkNavyBlue} color={colors.white} /></div>
                    <div><NormalBtn SView={this.props.SView} title="Cancel" bgColor={colors.white} color={colors.red} /></div>
                </div>
            </div>
        )
    }
}


export default Security;