import React from 'react';
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import "slick-carousel/slick/slick.css"; 
import "slick-carousel/slick/slick-theme.css";
import "animate.css/animate.min.css";
import { useMediaQuery } from 'react-responsive'
// modules
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
// Add FontName
import { englishFonts , persianFonts } from './data/fontName';
// Screens
import Home from './screens/Home';
import UserRegistration from './screens/UserRegistration';
import SearchRestaurant from './screens/SearchRestaurant';
import Restaurant from './screens/Restaurant';
import Shopping from './screens/Shopping';
import Dashboard from './screens/Dashboard';
import DashboardInvoice from './screens/DashboardInvoice';
// SmallView
import HomeSView from './screens/smallView/HomeSView';
import SearchResturantSView from './screens/smallView/SearchResturantSView';
import UserRegistrationSView from './screens/smallView/UserRegistrationSView';
import RestaurantSView from './screens/smallView/RestaurantSView';
import DashboardSView from './screens/smallView/DashboardSView';
import DashboardInvoiceSView from './screens/smallView/DashboardInvoiceSView';
import ShoppingSView from './screens/smallView/ShoppingSView';

const Desktop = ({ children }) => {
  const isDesktop = useMediaQuery({ minWidth: 769 })
  return isDesktop ? children : null
}
const Mobile = ({ children }) => {
  const isMobile = useMediaQuery({ maxWidth: 768 })
  return isMobile ? children : null
}

class App extends React.Component{
  constructor(props){
    super(props);
    this.state={
      active: false
    }
  }

  render(){
    return(
      <Router>
      <div id="App">
        <Switch>
          <Route exact path="/">
            <Desktop><Home/></Desktop>
            <Mobile><HomeSView/></Mobile>
          </Route>
          <Route exact path="/userRegistration">
            <Desktop><UserRegistration /></Desktop>
            <Mobile><UserRegistrationSView /></Mobile>
          </Route>
          <Route exact path="/searchRestaurant">
            <Desktop><SearchRestaurant /></Desktop>
            <Mobile><SearchResturantSView /></Mobile>
          </Route>
          <Route exact path="/restaurant">
            <Desktop><Restaurant /></Desktop>
            <Mobile><RestaurantSView /></Mobile>
          </Route>
          <Route exact path="/shopping">
            <Desktop><Shopping /></Desktop>
            <Mobile><ShoppingSView /></Mobile>
          </Route>
          <Route exact path="/dashboard">
            <Desktop><Dashboard /></Desktop>
            <Mobile><DashboardSView /></Mobile>
          </Route>
          <Route exact path="/dashboardInvocie">
            <Desktop><DashboardInvoice /></Desktop>
            <Mobile><DashboardInvoiceSView /></Mobile>
          </Route>
        </Switch>
      </div>
    </Router>
    )
  }
}
export default App;



// Global Variables
global.login = false
global.locate = "en"
global.font = ()=>{
  switch (global.locate) {
    case 'en' || 'tr':
      return englishFonts;
    case 'fa':
      return persianFonts;
    default:
      return englishFonts;
  }
}