import React from 'react'
import colors from '../../assets/colors';
import { NormalBtn } from '../Btn'
import MyInput from '../MyInput';
import AddressItem from '../AddressItem';
import Dropdown from '../Dropdown'
class UserProfileDashboard extends React.Component{
    constructor(props){
        super(props);
        this.state={}
    }
    render(){
        return (
            <div className="UserProfileDashboard">
                {/* Personal Information */}
                <h5 className={this.props.SView ? "FMT1" : "FT1"} style={{fontFamily:global.font().HeeboMedium,color:colors.darkNavyBlue,margin:0}}>Personal Information</h5>
                <div className={this.props.SView ? "Flex Col" : ""} style={{backgroundColor:colors.pink,padding:"20px",borderRadius:25,marginTop:15}}>
                    <div className={this.props.SView ? "Flex Col UserProfileTablet" : "Flex"} style={{marginTop:10,justifyContent:"space-between"}}> 
                        <div className="W100" style={{marginRight:this.props.SView ? 0 : 15}}><MyInput placeholder="Name" alt="Name" color={colors.lightGray} /></div>
                        <div className="W100" style={{marginLeft:this.props.SView ? 0 : 15}}><MyInput placeholder="Family" alt="Family" color={colors.lightGray} /></div>
                    </div>
                    <div className={this.props.SView ? "Flex Col UserProfileTablet" : "Flex"} style={{marginTop:10,justifyContent:"space-between"}}>
                        <div className="W100" style={{marginRight:this.props.SView ? 0 : 15}}><MyInput placeholder="Phone number" alt="PhoneNumber" color={colors.lightGray} /></div>
                        <div className="W100" style={{marginLeft:this.props.SView ? 0 : 15}}><MyInput disabled placeholder="SamuelJackson@gmail.com" alt="Email" color={colors.lightGray} /></div>
                    </div>
                    <div className="Flex" style={{marginTop:20,justifyContent:this.props.SView ? "center" : "flex-end"}}>
                        <NormalBtn SView={this.props.SView} fullWidth={this.props.SView} bgColor={this.props.SView ? colors.darkNavyBlue : "rgba(46, 38, 111,0.3)"} title="Confirm Changes" color={colors.white} />
                    </div>
                </div>
                {/* Address Information */}
                <div className={this.props.SView ? "Flex Col" : "Flex VCenter"} style={{justifyContent:"space-between",marginTop:50}}>
                    <h5 className={this.props.SView ? "FMT1" : "FT1"} style={{fontFamily:global.font().HeeboMedium,color:colors.darkNavyBlue,margin:0,marginBottom:this.props.SView ? 20 : 0}}>Address Information</h5>
                    <NormalBtn title="Add new address" SView={this.props.SView} fullWidth={this.props.SView} icon={require('../../assets/icons/add.svg')} bgColor={colors.red} color={colors.white} />
                </div>
                <AddressItem 
                    title="Home" 
                    onClick={()=>{this.setState({whichAddress:0})}} 
                    bgColor={colors.pink}
                    noDots
                    SView={this.props.SView}
                />
                <AddressItem 
                    title="WorkStation" 
                    onClick={()=>{this.setState({whichAddress:0})}} 
                    bgColor={colors.pink}
                    noDots
                    SView={this.props.SView}
                />
                <div style={{marginTop: 15,backgroundColor:colors.pink,padding:15,borderRadius:25}}>
                    <div className="BgImg MapBgImgAddressItem Flex HCenter VCenter">
                        <NormalBtn SView={this.props.SView} title={this.props.SView ? "Place your location" : "Place your location on map"} bgColor={colors.white} color={colors.red} />
                    </div>
                    <div className={this.props.SView ? "Flex W100 Col" : "Flex W100"} style={{marginTop:20}}>
                        <div className="W100" style={{paddingRight:this.props.SView ? 0 : 10}}>
                            <Dropdown 
                                title="City" 
                                onClick={(City)=>{this.setState({City},console.log(this.state.City))}} 
                                items={[{title:'Istanbul'},{title:'Ankara'},{title:'Ezmir'},{title:'Istanbul'},{title:'Ankara'},{title:'Ezmir'},{title:'Istanbul'},{title:'Ankara'},{title:'Ezmir'}]} 
                                SView={this.props.SView}
                            />
                        </div>
                        <div className="W100" style={{paddinLeft:this.props.SView ? 0 : 10,marginTop:this.props.SView ? 55 : 0}}>
                            <Dropdown 
                                title="Neighborhood" 
                                onClick={(Neighborhood)=>{this.setState({Neighborhood},console.log(this.state.Neighborhood))}} 
                                items={[{title:'Istanbul'},{title:'Ankara'},{title:'Ezmir'},{title:'Istanbul'},{title:'Ankara'},{title:'Ezmir'},{title:'Istanbul'},{title:'Ankara'},{title:'Ezmir'}]} 
                                SView={this.props.SView}
                            />
                        </div>
                    </div>
                    {!this.props.SView && <div className="Flex" style={{marginTop:65}}>
                        <MyInput placeholder="Full address ( Ave, Alley, No, Floor, Unit )" alt="address" color={colors.lightGray} />
                    </div>}
                    <div className={this.props.SView ? "Flex Col" : "Flex"} style={{marginTop:this.props.SView ? 65 : 10,justifyContent:"space-between"}}>
                        <div className="W100" style={{marginRight:10}}><MyInput placeholder="Mobile" alt="mobile" color={colors.lightGray} /></div>
                        <div className="W100" style={{marginLeft:this.props.SView ? 0 : 10}}><MyInput placeholder="Address Title ( e.g Home )" alt="addressTitle" color={colors.lightGray} /></div>
                    </div>
                    <div className="Flex VCenter" style={{justifyContent:"flex-end",marginTop:15}}>
                        <div className="Flex VCenter W100" style={{justifyContent:this.props.SView ? "space-between" : "flex-end"}}>
                            <div><NormalBtn SView={this.props.SView} title="Confirm" bgColor={colors.darkNavyBlue} color={colors.white} /></div>
                            <div style={{marginLeft:15}}><NormalBtn SView={this.props.SView} title="Cancel" bgColor={colors.white} color={colors.red} /></div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default UserProfileDashboard;